/* General container */
.linktreePage {
  padding: 0 1rem;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(232 204 88);
}


.dashboardContainer {
  z-index: 6;
  padding-top: 40px;
}

/* Main Heading */
.mainHeading {
  font-weight: 700;
  font-size: 32px;
  line-height: 2.25rem;
  margin-bottom: 1rem;
  letter-spacing: -1px;
  color: rgb(37, 79, 26);
}

.swipeContainer {
  height: 75px;
  overflow: hidden;
  width: 100%;
  color: rgba(59, 130, 246, 0.5);
}

.swipeContent {
  animation: swipeUp 12s infinite;
}

.swipeItem {
  overflow: hidden;
  height: 75px;
  max-width: 300px;
  text-wrap: balance;
  line-height: 34px;
}

/* Swipe Animation */
@keyframes swipeUp {
  0%, 15% {
    transform: translateY(0);
  }
  20%, 35% {
    transform: translateY(-20%);
  }
  40%, 55% {
    transform: translateY(-40%);
  }
  60%, 75% {
    transform: translateY(-60%);
  }
  80%, 95% {
    transform: translateY(-80%);
  }
}

/* Sub Heading */
.subHeading {
  font-weight: 400;
  line-height: 1.5rem;
  font-size: 14px;
  font-family: 'poppins';
  color: black;
  margin-bottom: 1.5rem;
}

/* Form */
.linktreeForm {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.usernameLabel {
  font-size: 0.875rem;
  color: #444;
  margin-bottom: 5px;
  position: relative;
}

.usernameInput {
  width: 250px;
  height: 55px;
  padding-left: 10px;
  font-size: 1rem;
  background-color: #f0f0f0;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-top: 5px;
  margin-bottom: 15px;
}

.claimButton {
  width: 200px;
  height: 45px;
  background-color: #254F1A;
  color: #D2E823;
  text-align: center;
  font-size: 1rem;
  padding: 10px;
  border-radius: 30px;
  border: none;
  cursor: pointer;
}

.claimButton span {
  font-weight: 600;
}

/* Footer */
.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 2rem;
}

.footerLinks {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.footerLink {
  font-size: 0.875rem;
  color: #254F1A;
  margin-bottom: 0.5rem;
  text-decoration: underline;
}

.signupButton {
  background-color: transparent;
  border: 1px solid #254F1A;
  color: #254F1A;
  padding: 12px 30px;
  border-radius: 30px;
  text-align: center;
  font-size: 0.875rem;
  margin-top: 1.5rem;
  cursor: pointer;
}

.footerImage {
  flex: 1;
  text-align: center;
}

.footerImage img {
  width: 150px;
  height: 226px;
  margin-top: -50px;
  margin-bottom: 30px;
}
